/* pacificoregular */
@font-face {
  font-family: 'pacificoregular';
  src: url('./font/pacifico-regular-webfont.woff2') format('woff2'),
       url('./font/pacifico-regular-webfont.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  background-color: whitesmoke;
  font-size: 1.8rem;
  height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.welcome-heading {
  font-family: pacificoregular;
}

.welcome-heading {
  font-size: 5rem;
}

.start-button {
  font-size: 1.3rem;
  width: 120px;
  padding: 20px;
  border-radius: 8px;
  border: 1px solid blue;
  background-color: blue;
  color: white;
  cursor: pointer;
  transition: all .4s ease;
}

button:hover {
  background-color: dodgerblue;
  border-color: dodgerblue;
}

.note {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.note-information {
  width: 80%;
  padding: 10px;
  background-color: tan;
  color: #333;
  position: relative;
  animation-name: fadeUp;
  animation-duration: 3s;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 40%);
  border-radius: 8px 0 8px 8px;
}

.note-information > .close {
  width: 2rem;
  height: 2rem;
  background-color: red;
  color: white;
  font-weight: bold;
  position: absolute;
  top: -40px;
  right: -10px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  cursor: pointer;
}

.note-information > img {
  max-width: 25%;
  filter: drop-shadow(1px 1px 5px #333);
}

@keyframes fadeUp {
  from {
    transform: translateY(100%);
  } to {
    transform: translateY(0);
  }
}

.main-app > .app-photo {
  max-width: 40%;
  border-radius: 8px;
  border: 1px solid #333;
  padding: 2px;
}

.main-app > form {
  width: 80%;
  padding: 15px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 18px;
  font-size: 1.3rem;
}

.main-app > form > label > input[type="radio"] {
  width: 1.8em;
  height: 1.8rem;
  accent-color: rgb(17, 233, 17);
}

.main-app > form > .question,
.main-app > form > input[type="submit"] {
  grid-column: 1 / -1;
}

.main-app > form > input[type="submit"] {
  width: 60px;
  height: 30px;
  background-color: dodgerblue;
  border-radius: 4px;
  border: 1px solid dodgerblue;
  justify-self: end;
  cursor: pointer;
}

.main-app > .yourscore {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: royalblue;
  color: whitesmoke;
  font-size: 1.5rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.yourscore > button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 2rem;
  transition: 1s;
}

.yourscore > button:hover {
  transform: scale(1.2, 1.2);
}

@media only screen and (max-width: 500px) {
  .main-app {
    font-size: 1.2rem;
  }

  .main-app > .app-photo {
    max-width: 75%;
  }

  .main-app > form {
    width: 90%;
    grid-template-columns: 1fr;
  }
}

@media only screen and (max-width: 330px) {
  .welcome-heading {
    font-size: 3rem;
  }
  .note-information {
    font-size: 1.5rem;
  }

  .main-app > .app-photo {
    width: 50%;
  }

  .main-app > form {
    font-size: 1rem;
    gap: 10px;
  }
}